import React, { useState } from "react";
import FormField from "../../../partials/FormField";
import { Question_Type } from "../../../../other/types/question.type";
import { EditedQuestion } from "../../../../other/types/edited-question.type";
import questionStoreSingleton from "../../../../other/mobx-stores/question.store";
import tagStoreSingleton from "../../../../other/mobx-stores/tag.store";


export type QuestionFields = 'question' | 'answer' | 'tags'

export default function Form_Sub({existingQuestion}: {existingQuestion?: Question_Type}){

  let initialQuestion: EditedQuestion = {value: '', correctAnswer: '', tagIds: []}
  if(existingQuestion){
    const {value, correctAnswer, tagIds} = existingQuestion
    initialQuestion = {value, correctAnswer, tagIds}
  }    

  const [editedQuestion, setQuestion] = useState(initialQuestion)
  const {value, correctAnswer, tagIds} = editedQuestion
  

  return (
    <div className="add-question">
      <h2>Add Question</h2>
      <FormField 
        name='question' 
        type='input'
        onUpdate={handleFieldUpdate} 
        value={value}
      />
      <FormField 
        name='answer' 
        type='textarea'
        onUpdate={handleFieldUpdate} 
        value={correctAnswer}
      />          
      <FormField 
        name='tags' 
        type='checkboxes'
        onUpdate={handleFieldUpdate} 
        tags={tagIds.map(tagStoreSingleton.getTag)}
      />  
      <button onClick={submitQuestion}>
        {existingQuestion ? 'Update' : 'Submit'}
      </button>
    </div>
  )

    
  function handleFieldUpdate(field: QuestionFields, value: any){
    setQuestion({...editedQuestion, [field]: value})
  }

  function submitQuestion(){
    if(existingQuestion){
      questionStoreSingleton.updateQuestion(existingQuestion.id, editedQuestion) 
    } else {
      questionStoreSingleton.addQuestion(editedQuestion) 
    }
  }
}