import {observable, action} from 'mobx';
import { Tag } from '../types/tag.type';
import { EditedTag } from '../types/edited-tag.type';
import st from './stub-tags'

function TagStore(){

  const stubTags: Tag[] = st.map(x => ({...x, dateLastUpdated: new Date()}))
  const tags: Tag[] = observable(stubTags)

  return {
    tags,
    addTag: action(addTag),
    getTag: action(getTag)
  }
  
  function addTag({value}: EditedTag){
    const tag: Tag = {
      id: new Date().getTime().toString(),
      value,
      dateLastUpdated: new Date()
    }
    tags.push(tag)
  }

  function getTag(id){
    return tags.find(t => t.id == id)
  }

}
const tagStoreSingleton = TagStore()

export default tagStoreSingleton