import { observable, action } from 'mobx';
import { Tag } from '../types/tag.type';
import { Quiz_Type } from '../types/quiz.type';
import { Question_Type } from '../types/question.type';


/* Quiz State */


let activeQuiz: Quiz_Type


interface QuizState_Interface {
  quizInProgress: boolean,
  answerSubmitted: boolean,
  correctnessRecorded: boolean,
  activeQuestion: Question_Type,
  selectedTags: Tag[]
}

const quizState: QuizState_Interface = observable({
  quizInProgress: false,
  answerSubmitted: false,
  correctnessRecorded: false,
  activeQuestion: null,
  selectedTags: []
})



function QuizStore() {
  return {
    quizState: observable(quizState),
    tagSelected: action(tagSelected),
    startQuiz: action(startQuiz)
  }
}
const quizStoreSingleton = QuizStore()
export default quizStoreSingleton






/* Quiz Actions */

  
function startQuiz(){
  quizState.quizInProgress = true
}

function tagSelected (selectedTag: Tag){
  const tagIndex = quizState.selectedTags.findIndex(tag => tag.id == selectedTag.id)
  const tagIsAlreadySelected = tagIndex >= 0
  if (tagIsAlreadySelected)
    quizState.selectedTags.splice(tagIndex, 1)
  else
    quizState.selectedTags.push(selectedTag)
}